define('CloudControl/models/profile', ['exports', 'ember-data'], function (exports, DS) {

    'use strict';

    var Profile = DS['default'].Model.extend({
        name: DS['default'].attr('String'),
        avatar: DS['default'].attr('String'),
        user: DS['default'].belongsTo('user'),
        verified: DS['default'].attr('Boolean', {
            defaultValue: false
        }),
        updated: DS['default'].attr('Date', {
            defaultValue: new Date()
        }),
        created: DS['default'].attr('Date', {
            defaultValue: new Date()
        })
    });

    exports['default'] = Profile;

});