define('CloudControl/controllers/login', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Controller.extend({
        password: 'password',
        identification: 'admin@admin.com',
        remember: false,
        /*    rememberMe: false,
            // change the store's cookie expiration time depending on whether "remember me" is checked or not
            rememberMeChanged: function() {
                this.get('session.store').cookieExpirationTime = this.get('rememberMe') ? 1209600 : null;
            }.observes('rememberMe'),
        */
        actions: {
            authenticate: function authenticate() {
                var _this = this;

                var authenticator = 'simple-auth-authenticator:jwt';
                var data = this.getProperties('identification', 'password');
                this.set('loginFailed', false);

                return this.get('session').authenticate(authenticator, data)['catch'](function (result) {
                    _this.set('loginFailedMessage', result);
                    _this.set('loginFailed', true);
                    throw result;
                });
            }
        }
    });

});