define('CloudControl/adapters/application', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

    'use strict';

    exports['default'] = DS['default'].RESTAdapter.extend({
        host: 'http://localhost:3000',
        namespace: 'api',
        headers: (function () {
            var token = this.container.lookup('session:custom').get('secure.token');
            return {
                'authorization': 'Bearer ' + token
            };
        }).property().volatile(),

        findQuery: function findQuery(store, type, query) {
            var loopbackQuery = {};

            Ember['default'].$.each(query, function (k, v) {
                loopbackQuery['filter[where][' + k + ']'] = v;
            });

            return this._super(store, type, loopbackQuery);
        }
    });

});